	import noData from '@/components/noData'
	import back from '@/components/back'
	import axios from 'axios'
	import $load from '@/utils/loading'
	export default {
		components: {
			noData,
			back
		},
		data() {
			return {
				temp:false,
				temp2:false,
				temp3:false,
				ownerInfo: {},
				marry:"",
				reproductive:"",
				taskRecord:null,
				medicalRecord:null,
				prescription:null,
				consultation:null,
				Patient: this.$store.state.patient,
				currentTime:"",
				bloodPre:{},//血压
				bloodSugar:{},//血糖
				bodyFat:{},//血脂
				isGetList:[],
				user: this.$store.state.userInfo,
				isPdf:false,
				patientId:this.$route.query.patientId
			}
		},
		created() {
			//获取当前时间
			this.getMsg()
			if(!this.Patient && this.patientId){
				this.$store.dispatch('pushpatient', {PatientId:this.patientId})
			}
		},
		methods: {
			toPreview(arr,idx){
	            const data = {
	                position: idx,
	                url: arr
	            }
	            this.$bridge.callhandler('Preview', JSON.stringify(data))
	        },
			getDate(day){
				//获取某天之前的日期
				let time = new Date();
			  	time.setDate(time.getDate() + day);//获取Day天后的日期 
			 	let y = time.getFullYear();
			  	let m = time.getMonth() + 1;//获取当前月份的日期 
			  	let d = time.getDate();
			  	return y + "-" + m + "-" + d;
			},
			getCurrentTime(){
				//获取当前时间
				let time = new Date();
				let h = time.getHours();
				let min = time.getMinutes();
				let s = time.getSeconds();
				return h + ":" + min + ":" + s;
			},
			getMsg() {
				axios.all([
					this.$post('PlatFormAPI/Category/QueryCategoryItem',{
		                CategoryIds: [ 2, 47 ]
		            }),
					this.$get('PlatFormAPI/PatientAccount/GetPatientHealthByPatientId', { //获取个人健康情况
						PatientId: this.patientId || this.Patient.PatientId
					})
				]).then(o => {
					//最近问诊
					this.getInquiry()
					
					this.temp = true
					//获取个人健康情况
					this.ownerInfo = o[1]
					//婚姻和生育状态的数字字典
					if(o[0] && o[0][0] && this.ownerInfo &&this.ownerInfo.MaritalStatus){
						o[0][0].Items.forEach(v => {
		                    if(this.ownerInfo.MaritalStatus == v.CategoryItemValue){
		                    	this.marry = v.CategoryItemName
		                    }
		                })
					}
					if(o[0] && o[0][1] && this.ownerInfo &&this.ownerInfo.ReproductiveStatus){
						o[0][1].Items.forEach(k => {
		                    if(this.ownerInfo.ReproductiveStatus== k.CategoryItemValue){
		                    	this.reproductive = k.CategoryItemName
		                    }
		                })
					}
				})
				
				
			},
			getInquiry(){
				//获取最近问诊
				$load.loadIn2()
				axios.all([
					this.$get('PlatFormAPI/Followup/GetNewTaskRecordForPatient', { //最近随访
						PatientId: this.patientId || this.Patient.PatientId
					}),
					this.$post('ConsultAPI/Consult/GetConsultRecordOfPatient', { //最近问诊
						PatientId: this.patientId || this.Patient.PatientId,
						PageIndex:1,
						PageSize:1
					}),
					this.$get('PlatFormAPI/PatientArchives/GetNewReportImageForPatient', { //最近病历
						PatientId: this.patientId || this.Patient.PatientId
					}),
					this.$post('Prescription/Prescription/GetPrescriptionListByPatientId', { //最近处方，即获取所有处方
						PatientId: this.patientId || this.Patient.PatientId,
						PageIndex:1,
						PageSize:2,
						SortDirection:true
					})
				]).then(o => {
					$load.loadOut2();
					this.getMonitor()
					this.temp2 = true
					//最近随访
					this.taskRecord = o[0]
					
					//最近问诊
					if(o[1].ReturnData && o[1].ReturnData.length >= 1){
						this.consultation = o[1].ReturnData[0]
					}
					//最近病历
					if(o[2] && o[2].ImgDetails && o[2].ImgDetails[0].FileType == 1){
	            		//	文件类型(0-图片，1-pdf)
	        			this.isPdf = true
	            	}
					this.medicalRecord = o[2]
					
					//最近处方
					if(o[3].ReturnData && o[3].ReturnData.length >= 1){
						this.prescription = o[3].ReturnData[0]
					}
				}).catch(err => {
					$load.loadOut2();
				})
			},
			getMonitor(){
				//最近检测
				axios.all([
					this.$post('MonitorAPI/Monitoring/GetJC_BloodPressureList', { //最近监测-血压
						PatientId: this.patientId || this.Patient.PatientId,
						StartTime:this.getDate(-7) + " 00:00:00",
		            	EndTime:this.getDate(0) + " " + this.getCurrentTime(),
						PageIndex:1,
		            	PageSize:1,
					}),
					this.$post('MonitorAPI/Monitoring/GetJC_BloodSugarDataList', { //最近监测-血糖
						PatientId: this.patientId || this.Patient.PatientId,
						StartTime:this.getDate(-7) + " 00:00:00",
		            	EndTime:this.getDate(0) + " " + this.getCurrentTime(),
						PageIndex:1,
		            	PageSize:1,
					}),
					this.$post('MonitorAPI/Monitoring/GetJC_BodyFatDataList', { //最近监测-血脂
						PatientId: this.patientId || this.Patient.PatientId,
						StartTime:this.getDate(-7) + " 00:00:00",
		            	EndTime:this.getDate(0) + " " + this.getCurrentTime(),
						PageIndex:1,
		            	PageSize:1,
					})
				]).then(o => {
					this.temp3 = true
					//最近监测 
					//血压监测
					if(o[0].ReturnData && o[0].ReturnData.length >= 1){
						this.bloodPre = o[0].ReturnData[0]
						this.isGetList[0]=true
					}
					//血糖监测
					if(o[1].ReturnData && o[1].ReturnData.length >= 1){
						this.bloodSugar = o[1].ReturnData[0]
						this.isGetList[1]=true
					}
					//体脂监测
					if(o[2].ReturnData && o[2].ReturnData.length >= 1){
						this.bodyFat = o[2].ReturnData[0]
						this.isGetList[2]=true
					}
					
				})
			},
			linkTo(o){
	            if(o.QuestionnaireUrl){
	                this.$bridge.callhandler('createWebView',o.QuestionnaireUrl)
	            }
	            if(o.LinkURL){
	                this.$bridge.callhandler('createWebView',o.LinkURL)
	            }
            },
            sendMsg(){
            	//最近问诊 - 点击更多进入IM
            	/*if(this.consultation){
            		let msg = this.consultation
            		this.$bridge.callhandler('SendMessage', JSON.stringify(msg))
            	}else{*/
            		//相当于发消息
            		axios.all([
						this.$post('IMAPI/Message/GetNullIMRelationRecord',{
			                DoctorId: this.user.Data.User.DoctorId,
		                	PatientId: this.patientId || this.Patient.PatientId
			            }),
						this.$get('PlatFormAPI/DoctorPatient/QueryDoctorPatientSummary', { //获取个人健康情况
							PatientId: this.patientId || this.Patient.PatientId,
			                DrId: this.user.Data.User.DoctorId,
						})
					]).then(o=>{
						let msg = o[0]
						msg.PatientInfo = {
							PatientId: this.patientId || this.Patient.PatientId,
							PatientName: this.Patient.PatientName,
							PatientSex: this.Patient.PatientSex,
							Birthday: this.Patient.Birthday,
							Age: this.Patient.Age,
						}
						msg.DoctorPatientRelation = {
							DrPatientId: o[1].DrPatientId,
						}
						this.$bridge.callhandler('SendMessage', JSON.stringify(msg))
					})
            	/*}*/
           	},
           	checkPdf(){
           		//查看pdf
           		if(this.medicalRecord && this.medicalRecord.ImgDetails && this.medicalRecord.ImgDetails[0].FileType == 1){
	            	//	文件类型(0-图片，1-pdf)
	        		//通知App打开pdf报告
					let obj = {
						title :this.medicalRecord.DiseaseDesc,
						linkUrl : this.medicalRecord.ImgDetails[0].ImagePaths[0]
					}
					this.$bridge.callhandler('showPdfReport', JSON.stringify(obj))
	            }
           	}
		}
	}



